>\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:14.0pt;\"><span style=\"font-size:14px;\">Reward amount</span><span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">20%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">5%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">3%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">3%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">5%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">2%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">2%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t</tbody>\r\n</table>\r\n<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>3. Remarks on sharing reward mechanism:</b>\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<span style=\"text-indent:-18pt;\">1. The 1% reward of the upper and lower 20 levels of global public volleyball rewards is calculated based on the coins invested by the other party and the coin rewards for reinvestment.</span>\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<span style=\"text-indent:-18pt;\">2. Participating in global public row feedback and direct sharing rewards will be rewarded with tokens, and will also be rewarded with equivalent computing power to participate in deflationary mining pool mining. Participating in the deflation mining pool, the income generated by the rewarded computing power is equal to 1U=1 computing power to offset each other.</span>\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<span style=\"text-indent:-18pt;\">3. If the minimum 5 direct push tasks are not completed in the global public volleyball feedback, the maximum profit can only be withdrawn at 40% and 60% reinvested. And in the direct sharing rewards, those who do not purchase 400U computing power and complete 5 direct pushes can only enjoy the 4th generation rewards.</span>\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;\">\r\n\t<b>NftList&#039;s DEFI mining pool revenue mechanism</b><b><span></span><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>1. Revenue from deflationary mining pools: NTS ratio 60 million</b><b><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>1. The amount of currency produced by deflationary mining pools</b><b><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t(Total global investment computing power + total investment computing power reinvestment) × 20% = deflationary mining pool currency production<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>2. Personal deflationary mining pool income</b><b><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\tPersonal promotion computing power ÷ (global investment computing power + global promotion computing power) × deflationary mining pool currency production = personal deflationary mining pool income<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<a name=\"_Hlk81914791\"></a> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>3. Notes on the income of deflationary mining pools:</b><b><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t(1) The income of individual deflationary mining pools is settled daily, and all data accounting standards are 24 hours.<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t(2) Personal promotion computing power specifically refers to the amount of initial investment of computing power, excluding reinvestment quota.<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t(3) The total amount of currency produced by the deflationary mining pool will be destroyed, allowing NTS to achieve the ultimate deflationary effect.<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>2. Income from pledged mining pool: NTS ratio 20 million</b><span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>1. Pledge computing power</b><b><span></span></b> \r\n</p>\r\n<table class=\"MsoTableGrid\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\" style=\"border-collapse:collapse;border:none;\">\r\n\t<tbody>\r\n\t\t<tr>\r\n\t\t\t<td width=\"102\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\tNTS ratio\r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"104\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">Mainstream currency +20%<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">Mainstream currency +40%<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">Mainstream currency +60%<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">NTS100%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td width=\"102\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">Hashrate ratio<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"104\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=1power</span>\r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=1.5power</span>\r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=2power</span>\r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=3power</span>\r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t</tbody>\r\n</table>\r\n<p class=\"MsoNormal\">\r\n\t<b>2. Pledge the amount of coins produced by the mining pool</b>\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\tThe minimum daily output of pledged mining pools is 20,000 coins. When the daily pledge computing power reaches 10 million, the coin production will increase by 20,000, and the increased computing power will be less than 10 million, and the coin production will not be increased; if the daily computing power is less than 10 million, the distribution of coins will be reduced according to the ratio of 500:1 . All unallocated coins on the day will be destroyed.\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>3. Personal pledge of mining pool income</b><b><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\tPersonal pledged computing power ÷ global pledged computing power × pledged mining pool currency production = personal pledged mining pool income<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>4. Notes on pledged mining pool income:</b>\r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<span style=\"text-indent:-36pt;\">&nbsp;&nbsp;&nbsp;&nbsp;(1) The tokens pledged by the pledged mining pool can be settled at any time and the income can be freely entered and exited.</span>\r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<span style=\"text-indent:-36pt;\">&nbsp;&nbsp;&nbsp;&nbsp;(2) The withdrawal fee is 10%. The distribution is as follows: 50% destruction, 10% nodes, 5% super nodes, 35% foundation.</span>\r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;\">\r\n\t<b>NftList Fund reserves the right of final interpretation of this investment notice.</b>\r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;\">\r\n\t<b style=\"text-indent:42pt;\">I have read in detail and agree with the above NftList participation notice</b>\r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:52.5pt;text-indent:-42.0pt;\">\r\n\t<b></b> \r\n</p>"
    }
  },
}
</script>

